.header-nav {
  width: 100%;
  background-color: #f8f8f8;
  padding: 15px 0;
  display: flex;
  justify-content: center;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.1);
}

.header-nav ul {
  display: flex;
  gap: 25px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.header-nav a {
  text-decoration: none;
  color: #333;
  font-weight: 500;
  font-size: 18px;
}

.header-nav a:hover {
  color: #007bff;
}
