.footer {
  width: 100%;
  text-align: center;
  padding: 15px 0;
  margin-top: 40px;
  background-color: #f8f8f8;
  font-size: 16px;
  color: #444;
  box-shadow: 0px -1px 4px rgba(0, 0, 0, 0.07);
}

.footer a {
  text-decoration: none;
  color: #007bff;
}

.footer a:hover {
  text-decoration: underline;
}
