.project-page {
  text-align: center;
  padding: 40px;
}

.project-item {
  margin: 20px auto;
  max-width: 600px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  background: #f8f9fa;
}

.project-item h2 {
  font-size: 22px;
  margin-bottom: 10px;
}

.project-item a {
  text-decoration: none;
  color: #007bff;
  font-weight: bold;
}

.project-item a:hover {
  text-decoration: underline;
}
